import Vue from 'vue'

const clone = e => JSON.parse(JSON.stringify(e))
const getPaletteList = function(palette = []) {
  const paletteList = []
  for (let i = 0; i < 160; i++) {
    if (palette[i]) {
      paletteList.push(palette[i])
    } else {
      paletteList.push('4b4b4b')
    }
  }
  return paletteList
}

let saveData = []
let initState = {
  width: 2000,
  height: 2000,
  gridSize: 10,
  size: 200,
  x: -500,
  y: -500,
  colorIndex: 0,
  brushIndex: 'brush',
  layerIndex: '',
  saveIndex: 0,
  drawIndex: 0,
  showGrid: true,
  showLayer: true,
  showAlpha: false,
  paletteList: getPaletteList([]),
  layer: [],
  selects: []
}

try {
  const pt = JSON.parse(localStorage.getItem('paletteList'))
  const lr = JSON.parse(localStorage.getItem('layer'))
  if (pt && lr) {
    initState.paletteList = pt
    initState.layer = lr
    initState.layerIndex = lr[0].uid
    saveData[0] = {
      paletteList: clone(pt),
      selects: [],
      layer: clone(lr)
    }
  }
} catch (error) {
  initState.layer = [{
    name: '图层新建副本',
    show: true,
    data: [],
    uid: '1234'
  }]
  initState.layerIndex = initState.layer[0].uid
  saveData[0] = {
    paletteList: clone(initState.paletteList),
    selects: [],
    layer: clone(initState.layer)
  }
}

window.initState = initState
window.saveData = saveData
const state = Vue.observable(initState)
Vue.prototype.$state = state
Vue.prototype.$save = function () {
  const paletteList = clone(state.paletteList)
  const layer = clone(state.layer)
  const selects = clone(state.selects)
  state.saveIndex += 1
  state.drawIndex += 1
  saveData[state.saveIndex] = {
    paletteList,
    selects,
    layer
  }
  state.paletteList = paletteList
  state.layer = layer
  localStorage.setItem('paletteList', JSON.stringify(paletteList))
  localStorage.setItem('layer', JSON.stringify(layer))
}
Vue.prototype.$back = function () {
  state.saveIndex -= 1
  if (state.saveIndex < 0) state.saveIndex = 0
  const data = saveData[state.saveIndex]
  if (data) {
    state.paletteList = clone(data.paletteList)
    state.layer = clone(data.layer)
    state.selects = []
  }
  state.drawIndex += 1
}
Vue.prototype.$forward = function () {
  state.saveIndex += 1
  if (state.saveIndex > saveData.length - 1) state.saveIndex = saveData.length - 1
  const data = saveData[state.saveIndex]
  if (data) {
    state.paletteList = clone(data.paletteList)
    state.layer = clone(data.layer)
    state.selects = []
  }
  state.drawIndex += 1
}
